.buddit-p{
    margin: 0.1em 0.5em;
}

.buddit-body{
    box-shadow: 0px 2px 1px -2px rgba(0,0,0,0.1);
    margin-top: 1em;
}

.c1{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.c2{
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
}

.highlight-username{
    font-size: 1.3em;
    color: mediumvioletred;
}

.sub-comment{
    margin-left: 1em;
}

.to-bold{
    font-weight: bold;
}

.pointable{
    cursor: pointer;
}

.highlight-title{
    font-size: 1.3em;
}

.text-btn{
    margin-left: 0.5em;
}

.imag-stuff{
    max-width:100%;
    max-height:100%;
}

.submitted{
    color: rgb(200,200,200);
    font-size: 0.9em;
}
